import { Button } from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers';
import dayjs from 'dayjs';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { PhoneNumberClient } from '../../api/server';
import { useNavigate, useParams } from 'react-router-dom';

export default function Holidays() {
  const globalInfo = useSelector((state) => state.globalInfo)
  let { currentPhoneNumberId } = useParams();
  const navigate = useNavigate();

  const [holiday, setHoliday] = React.useState(null);
  const [holidays, setHolidays] = React.useState([]);

  function deleteHoliday(index){
    var tempHolidays = [...holidays];

    tempHolidays.splice(index, 1)
    const client1 = new PhoneNumberClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);
    client1.updateHolidays(currentPhoneNumberId, JSON.stringify(tempHolidays)).then((data)=>{
      reload();
    })
  }
  function addHoliday(){
    var tempHolidays = [...holidays];

    tempHolidays.push(holiday.$d.toLocaleDateString());

    const client1 = new PhoneNumberClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);
    client1.updateHolidays(currentPhoneNumberId, JSON.stringify(tempHolidays)).then((data)=>{
      reload();
    })
  }
  const onClose = () =>{
    navigate('/phonenumbers')
}
  function reload(){
    const client1 = new PhoneNumberClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);

    if (currentPhoneNumberId) {

      client1.getPhoneNumberById(currentPhoneNumberId).then((data) => {
        if (data.holidays) {
          setHolidays(JSON.parse(data.holidays))
        }
      })
    }
  }
  useEffect(() => {
    reload();
  }, [currentPhoneNumberId])

  return (
    <div style={{ padding: 20, width: 500, height: 500 }}>

      <h2>Holidays</h2>
      {
        holidays.map((item, index)=>{
          return <div key={index}>{(new Date(item)).toDateString()}<Button onClick={()=>deleteHoliday(index)}>Delete</Button></div>
        })
      }
      <div>
        <DatePicker
          value={holiday}
          onChange={(newValue) => { setHoliday(newValue) }}
        />
        <Button variant='outlined' onClick={()=>{addHoliday()}}>Add</Button>
      </div>
    </div>
  )
}
