import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Button } from '@mui/material';
import { useSelector } from 'react-redux';
import { PhoneNumberClient } from '../../api/server';
import { Link } from 'react-router-dom';

const PhoneNumbers = () => {
    const [phoneNumbers, setPhoneNumbers] = React.useState( [])
    const globalInfo = useSelector((state) => state.globalInfo)

    function reload(){
        const client = new PhoneNumberClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);

        client.getPhoneNumbers(globalInfo.currentCompanyId).then((data)=>{
            setPhoneNumbers (data);
           // console.log(data)
       })
    }
    
    function followupActionName(action){
        
        if(!action) return "";


        var x = JSON.parse(action);
        
        //console.log(x);
        
        if(x.followUpAction && x.followUpAction.name){
            return x.followUpAction.name;
        }
        else {
            return ""
        }
        
    }

   
    React.useEffect(()=>{
        reload();
        
    }, [globalInfo])

   
    return (
        <div>

            {

                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                            <TableCell>Phone Number</TableCell>
                            <TableCell>Points to</TableCell>
                                <TableCell>
                                    {globalInfo.currentCompanyId && globalInfo.accessToken && globalInfo.isAdministrator && <Button variant='outlined' component={Link} to={`/addphonenumber`}>Add Phone Number</Button>
                                    }
                                </TableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {phoneNumbers.map((row) => (
                                <TableRow
                                    key={row.phoneNumberId}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.phoneNumberDigits.getFormattedPhoneNum()} 
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                  {JSON.parse(row.action).name}<br></br> 
                                   
                                   
                                    {
                                        followupActionName(row.action) && <>
                                            <>
                                                Followed by: 
                                            </>
                                            <>
                                            {followupActionName(row.action)}
                                            </>
                                        </>
                                    }
                                    </TableCell>
                                    <TableCell>

                                    <Button variant='contained' component={Link} to={`/holidays/${row.phoneNumberId}`}>Holidays</Button> &nbsp; &nbsp;
                                    <Button variant='contained' component={Link} to={`/businesshours/${row.phoneNumberId}`}>Business Hours</Button> &nbsp; &nbsp;
                                    <Button variant='contained' component={Link} to={`/editphonenumber/${row.phoneNumberId}`}>Edit</Button>

                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            }
        </div>

    )
}

export default PhoneNumbers